import React, { useState } from 'react';
import styled from "styled-components";

import logo_wide from '../images/Logo_wide.png';
import Theme from '../pages/Theme';

function Toolbar() {
  const [escolheTheme, setEscolheTheme] = useState(() => {
    const localTheme = window.localStorage.getItem("theme");

    if (localTheme)
      return false;
    else
      return true;

  });

  return (
    <Header >
      <Content>
        <div>
          <img src={logo_wide} alt="Seven Galaxies" />
        </div>
        <button onClick={() => setEscolheTheme(true)} > ..... </button>
      </Content>
      <Theme
        onClose={() => setEscolheTheme(false)}
        escolheTheme={escolheTheme} />
    </Header>
  );
}

const Header = styled.header`
  /* display: flex;
  flex-direction: column;
  align-items: center; */
  /* box-shadow: 1px 10px 18px 7px #000; */
  background-image: linear-gradient(to top,rgb(0 0 0 / 0%),rgba(0, 0, 0, .80));
  /* display: grid;
  grid-template-columns: 3fr 1fr;
  align-items: center;
  box-shadow: 1px 10px 18px 7px #000; */
  
  
`;

const Content = styled.div` 
  display: grid;
  grid-template-columns: 20fr 1fr;
  align-items: center;
  
  /* display:flex;
  flex-direction: column;
  align-items: center;
  width: calc(100px + 20vmin); */
  div {
    display: flex;
    flex-direction: column;
    align-items: center; 
  }
  img {
    width: calc(50vmin);
  }
  button {
    width: calc(30px);
  }
`;



export default Toolbar;