import React from 'react';
import { Link } from 'react-router-dom';

import logo from '../images/Logo.png';


function Menu() {

  window.localStorage.setItem("theme", '');

  return (
    <div className="App" >
      <main>
        <Link to="/sevengalaxies-app/Game">
          <img src={logo} alt="Seven Galaxies" width="80%" />
        </Link>
      </main>
    </div>
  )
}

export default Menu;