import React, { useState } from 'react';
import '../components/Modal.css';
import { ThemeProvider } from "styled-components";
import { SevenGalaxiesTheme, GaiaTheme, StrojTheme } from "../themes";
import GlobalTheme from "../globals";


function Theme(props) {
  const [theme, setTheme] = useState(window.localStorage.getItem("theme"));


  function handleAction(tema) {
    window.localStorage.setItem("theme", tema);
    const localTheme = window.localStorage.getItem("theme");
    localTheme && setTheme(localTheme);

    props.onClose();
  }

  // useEffect(() => {
  //   const localTheme = window.localStorage.getItem("theme");
  //   localTheme && setTheme(localTheme);
  // }, []);

  return (
    <>
      <ThemeProvider theme={theme === "Stroj" ? StrojTheme : (theme === "Gaia" ? GaiaTheme : SevenGalaxiesTheme)}>
        <GlobalTheme />
      </ThemeProvider>

      {props.escolheTheme
        ? <div className="modal">
          <div className="container">
            <div className="message">
              Escolha o Tema
          </div>
            <div className="options">
              <button className="action" onClick={() => handleAction('7G')}>
                <div>7G Theme</div>
              </button>
              <button className="action" onClick={() => handleAction('Gaia')}>
                <div>Gaia Theme</div>
              </button>
              <button className="action" onClick={() => handleAction('Stroj')}>
                <div>Stroj Theme</div>
              </button>
            </div>
          </div>
        </div>
        : null}
    </>

  )

}

export default Theme;